/* SearchPanel.css - Search Component Styles */

.search-panel {
  position: absolute;
  top: 20px;
  left: 20px;
  min-width: 280px;
  max-width: 350px;
  background: rgba(15, 15, 25, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

.search-panel:hover {
  border-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 15px 60px rgba(0, 0, 0, 0.6);
}

.search-panel.expanded {
  box-shadow: 0 20px 80px rgba(0, 0, 0, 0.7);
}

.search-header {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;
  position: relative;
}

.search-header:hover {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px 15px 0 0;
}

.search-content {
  padding: 0 15px 15px;
  animation: slideDown 0.3s ease-out;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: -1px;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
    max-height: 0;
  }
  to {
    opacity: 1;
    transform: translateY(0);
    max-height: 500px;
  }
}

.search-input-container {
  position: relative;
  margin: 15px 0;
}

.search-input {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 12px 16px;
  padding-right: 40px;
  border-radius: 10px;
  font-size: 14px;
  width: 100%;
  transition: all 0.2s ease;
  font-family: inherit;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.clear-search {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  font-size: 16px;
  padding: 2px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.clear-search:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  transform: translateY(-50%) scale(1.1);
}

.suggestions-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(20, 20, 30, 0.98);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  margin-top: 5px;
  z-index: 1001;
  max-height: 200px;
  overflow-y: auto;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.6);
  animation: dropdownAppear 0.2s ease-out;
}

@keyframes dropdownAppear {
  from {
    opacity: 0;
    transform: translateY(-5px) scale(0.98);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.suggestion-item {
  padding: 12px 16px;
  cursor: pointer;
  transition: all 0.15s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.suggestion-item:last-child {
  border-bottom: none;
}

.suggestion-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateX(5px);
}

.suggestion-title {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 4px;
  color: white;
  line-height: 1.3;
}

.suggestion-subtitle {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
  font-style: italic;
  margin-bottom: 4px;
  line-height: 1.3;
}

.suggestion-cluster {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  gap: 4px;
}

.quick-actions {
  display: flex;
  gap: 10px;
  margin: 15px 0;
  flex-wrap: wrap;
}

.quick-actions .action-button {
  flex: 1;
  min-width: 120px;
  font-size: 12px;
  padding: 8px 12px;
  justify-content: center;
}

.search-stats {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  padding: 8px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  margin-top: 10px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .search-panel {
    top: 10px;
    left: 10px;
    right: 10px;
    max-width: none;
    min-width: auto;
  }
  
  .search-header {
    padding: 12px;
  }
  
  .search-content {
    padding: 0 12px 12px;
  }
  
  .search-input {
    padding: 10px 12px;
    padding-right: 36px;
  }
  
  .quick-actions {
    flex-direction: column;
  }
  
  .quick-actions .action-button {
    min-width: auto;
  }
}

/* Dark mode enhancements */
@media (prefers-color-scheme: dark) {
  .search-panel {
    background: rgba(10, 10, 15, 0.98);
  }
  
  .suggestions-dropdown {
    background: rgba(15, 15, 20, 0.98);
  }
}