/* ClusterPanel.css */
.cluster-panel {
  position: absolute;
  top: 20px;
  right: 20px;
  min-width: 280px;
  max-width: 320px;
  background: rgba(15, 15, 25, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

.cluster-panel:hover {
  border-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 15px 60px rgba(0, 0, 0, 0.6);
}

.cluster-header {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;
}

.cluster-header:hover {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px 15px 0 0;
}

.cluster-count {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.8);
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.cluster-content {
  padding: 0 15px 15px;
  animation: slideDown 0.3s ease-out;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: -1px;
}

.cluster-controls {
  display: flex;
  gap: 10px;
  margin: 15px 0;
  flex-wrap: wrap;
}

.cluster-list {
  max-height: 300px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
}

.cluster-list::-webkit-scrollbar {
  width: 6px;
}

.cluster-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.cluster-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  margin: 8px 0;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.cluster-item:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateX(-3px);
  border-color: rgba(255, 255, 255, 0.2);
}

.cluster-item.hidden {
  opacity: 0.4;
  background: rgba(255, 255, 255, 0.02);
}

.cluster-info {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-grow: 1;
}

.cluster-color {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.2);
  flex-shrink: 0;
}

.cluster-details {
  flex-grow: 1;
}

.cluster-name {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 2px;
}

.cluster-count-text {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.6);
}

.cluster-toggle {
  font-size: 16px;
  opacity: 0.7;
  transition: all 0.2s ease;
}

.cluster-item:hover .cluster-toggle {
  opacity: 1;
  transform: scale(1.1);
}

.cluster-legend {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 12px;
  margin-top: 15px;
}

.legend-title {
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #667eea;
}

.legend-item {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.7);
  margin: 4px 0;
  line-height: 1.4;
}