/* PostDetailModal.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: modalOverlayAppear 0.3s ease;
  padding: 20px;
}

@keyframes modalOverlayAppear {
  from {
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  to {
    opacity: 1;
    backdrop-filter: blur(10px);
  }
}

.modal-content {
  background: rgba(20, 20, 30, 0.95);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  width: 100%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 25px 100px rgba(0, 0, 0, 0.8);
  animation: modalContentAppear 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes modalContentAppear {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.modal-header {
  display: flex;
  align-items: flex-start;
  padding: 25px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  gap: 15px;
}

.modal-cluster-indicator {
  width: 6px;
  height: 60px;
  border-radius: 3px;
  flex-shrink: 0;
}

.modal-title {
  font-size: 20px;
  font-weight: 700;
  margin: 0;
  flex-grow: 1;
  line-height: 1.4;
}

.modal-close-button {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  font-size: 20px;
  padding: 5px;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.modal-close-button:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  transform: scale(1.1);
}

.modal-body {
  padding: 25px;
}

.post-meta {
  margin-bottom: 25px;
}

.meta-item {
  display: flex;
  align-items: center;
  margin: 12px 0;
  gap: 10px;
}

.meta-label {
  font-size: 14px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  min-width: 100px;
}

.meta-value {
  font-size: 14px;
  color: white;
}

.cluster-badge {
  padding: 4px 12px;
  border-radius: 15px;
  border: 1px solid;
  font-weight: 600;
  font-size: 12px;
}

.post-preview {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  border-left: 4px solid #667eea;
}

.preview-header {
  font-size: 16px;
  font-weight: 600;
  color: #667eea;
  margin-bottom: 15px;
}

.preview-content {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
}

.preview-content p {
  margin: 12px 0;
  font-size: 14px;
}

.modal-footer {
  display: flex;
  justify-content: space-between;
  padding: 25px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  gap: 15px;
  flex-wrap: wrap;
}

.modal-footer .action-button {
  flex: 1;
  min-width: 150px;
  justify-content: center;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .cluster-panel,
  .stats-panel {
    position: fixed;
    top: auto;
    right: 10px;
    left: auto;
    bottom: 10px;
    min-width: auto;
    width: calc(50% - 15px);
  }

  .stats-panel {
    right: auto;
    left: 10px;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .modal-content {
    max-width: none;
    margin: 10px;
    border-radius: 15px;
  }
  
  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 20px;
  }
  
  .modal-footer {
    flex-direction: column;
  }
  
  .modal-footer .action-button {
    min-width: auto;
  }
}