/* Header.css - Visual Title Component Styles */

.header-panel {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  min-width: 320px;
  max-width: 800px;
  width: 90%;
  background: rgba(15, 15, 25, 0.95);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);
}


.header-panel:hover {
  border-color: rgba(255, 255, 255, 0.25);
  box-shadow: 0 25px 80px rgba(0, 0, 0, 0.7);
}

.header-panel.minimized {
  min-width: 280px;
  max-width: 600px;
}

.header-content {
  position: relative;
  padding: 25px;
}

.header-panel.minimized .header-content {
  padding: 15px;
}

.header-main {
  text-align: center;
}

.header-title {
  margin: 0 0 15px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.title-main {
  font-size: 32px;
  font-weight: 800;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
  text-shadow: 0 0 30px rgba(102, 126, 234, 0.3);
}

.title-sub {
  font-size: 16px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 2px;
  text-transform: uppercase;
}

.header-description {
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.description-text {
  font-size: 16px;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.85);
  margin: 0 0 20px 0;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.description-text strong {
  color: #667eea;
  font-weight: 600;
}

.quick-guide {
  display: flex;
  justify-content: center;
  gap: 25px;
  flex-wrap: wrap;
  margin-top: 15px;
}

.guide-item {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.75);
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.2s ease;
}

.guide-item:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

.guide-item strong {
  color: white;
  font-weight: 500;
}

.minimize-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.2s ease;
  color: rgba(255, 255, 255, 0.7);
}

.minimize-button:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
  transform: scale(1.05);
  color: white;
}

.minimized-info {
  text-align: center;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 10px;
  font-weight: 500;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .header-panel {
    top: 10px;
    width: calc(100% - 20px);
    min-width: auto;
  }

  .header-content {
    padding: 20px;
  }

  .header-panel.minimized .header-content {
    padding: 12px;
  }

  .title-main {
    font-size: 28px;
  }

  .title-sub {
    font-size: 14px;
    letter-spacing: 1px;
  }

  .description-text {
    font-size: 15px;
    line-height: 1.5;
  }

  .quick-guide {
    gap: 15px;
  }

  .guide-item {
    font-size: 13px;
    padding: 6px 10px;
  }

  .minimize-button {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .quick-guide {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .guide-item {
    width: fit-content;
  }
}