/* StatsPanel.css */
.stats-panel {
  position: absolute;
  bottom: 20px;
  left: 20px;
  min-width: 280px;
  max-width: 320px;
  background: rgba(15, 15, 25, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

.stats-panel:hover {
  border-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 15px 60px rgba(0, 0, 0, 0.6);
}

.stats-header {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;
}

.stats-header:hover {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px 15px 0 0;
}

.stats-content {
  padding: 0 15px 15px;
  animation: slideDown 0.3s ease-out;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: -1px;
}

.stats-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin: 15px 0;
}

.stat-item {
  text-align: center;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.2s ease;
}

.stat-item:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
  color: #667eea;
  margin-bottom: 5px;
  text-shadow: 0 0 10px rgba(102, 126, 234, 0.3);
}

.stat-label {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.visibility-bar {
  margin: 15px 0;
}

.visibility-label {
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 8px;
  color: rgba(255, 255, 255, 0.8);
}

.progress-bar {
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.hover-info {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 12px;
  margin: 15px 0;
  border-left: 3px solid #667eea;
}

.hover-title {
  font-size: 12px;
  font-weight: 600;
  color: #667eea;
  margin-bottom: 8px;
}

.hover-post-title {
  font-size: 13px;
  font-weight: 500;
  margin-bottom: 4px;
}

.hover-post-cluster {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
}

.performance-info {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 12px;
  margin-top: 15px;
}

.performance-title {
  font-size: 12px;
  font-weight: 600;
  color: #2ecc71;
  margin-bottom: 8px;
}

.performance-item {
  display: flex;
  justify-content: space-between;
  font-size: 11px;
  margin: 4px 0;
  color: rgba(255, 255, 255, 0.7);
}

.performance-value {
  color: #2ecc71;
  font-weight: 600;
}